/* @2x.png image source*/
.border-none:before {
  display: none;
}
.border-none:after {
  display: none;
}
.settings {
  background-color: #f8f8f8;
}
.settings .header {
  border-bottom: 1px solid #e8e8e8;
  margin-bottom: 9px;
  background-color: #fff;
  height: 64px;
  line-height: 64px;
  text-align: center;
  font-weight: bold;
  color: #363636;
  position: relative;
}
.settings .header .back_to {
  background-image: url('back.png');
}
@media only screen and (-webkit-min-device-pixel-ratio: 2), only screen and (min-device-pixel-ratio: 2) {
.settings .header .back_to {
    background-image: url('back@2x.png');
}
}
@media only screen and (-webkit-min-device-pixel-ratio: 3), only screen and (min-device-pixel-ratio: 3) {
.settings .header .back_to {
    background-image: url('back@3x.png');
}
}
.settings .content {
  background-color: #fff;
  border-bottom: 1px solid #e8e8e8;
  margin-bottom: 9px;
}
.settings .content a,
.settings .content .subscription {
  display: block;
  height: 48px;
  line-height: 48px;
  text-indent: 13px;
  font-size: 15px;
  color: #363636;
  border-bottom: 1px solid #f2f2f2;
  background-repeat: no-repeat;
  background-size: 8px 13px;
  background-position: 93% center;
  background-image: url('right.png');
}
@media only screen and (-webkit-min-device-pixel-ratio: 2), only screen and (min-device-pixel-ratio: 2) {
.settings .content a,
  .settings .content .subscription {
    background-image: url('right@2x.png');
}
}
@media only screen and (-webkit-min-device-pixel-ratio: 3), only screen and (min-device-pixel-ratio: 3) {
.settings .content a,
  .settings .content .subscription {
    background-image: url('right@3x.png');
}
}
.settings .content a:last-child {
  border: none;
}
.settings .content .subscription {
  background: #fff;
  position: relative;
}
.settings .content .subscription .switch {
  position: absolute;
  top: 10px;
  right: 10px;
  display: block;
  width: 50px;
  height: 29px;
  background-size: 50px 29px;
  background-repeat: no-repeat;
  background-image: url('Switch.png');
}
@media only screen and (-webkit-min-device-pixel-ratio: 2), only screen and (min-device-pixel-ratio: 2) {
.settings .content .subscription .switch {
    background-image: url('Switch@2x.png');
}
}
@media only screen and (-webkit-min-device-pixel-ratio: 3), only screen and (min-device-pixel-ratio: 3) {
.settings .content .subscription .switch {
    background-image: url('Switch@3x.png');
}
}
.settings .content .subscription-true .switch {
  background-image: url('Switch_on.png');
}
@media only screen and (-webkit-min-device-pixel-ratio: 2), only screen and (min-device-pixel-ratio: 2) {
.settings .content .subscription-true .switch {
    background-image: url('Switch_on@2x.png');
}
}
@media only screen and (-webkit-min-device-pixel-ratio: 3), only screen and (min-device-pixel-ratio: 3) {
.settings .content .subscription-true .switch {
    background-image: url('Switch_on@3x.png');
}
}
.settings .footer {
  height: 50px;
  line-height: 50px;
  text-align: center;
  font-size: 15px;
  color: #fe6f69;
  background-color: #fff;
}



