/* @2x.png image source*/
.bg-image(@url){
  background-image: ~"url('@{url}.png')";
  @media only screen and (-webkit-min-device-pixel-ratio:2.0),only screen and (min-device-pixel-ratio:2.0){
    background-image: ~"url('@{url}@2x.png')";
  }
  @media only screen and (-webkit-min-device-pixel-ratio:3.0),only screen and (min-device-pixel-ratio:3.0){
    background-image: ~"url('@{url}@3x.png')";
  }
};

.border-top-1px-dotted(@color){
	position: relative;
	&:before{
		content: '';
		display: block;
		position: absolute;
		width: 100%;
		left: 0;
		top: 0;
		border-top: 1px dotted @color; 
		@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5){
			-webkit-transform: scaleY(0.7);
			transform: scaleY(0.7);
		}

		@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2){
			-webkit-transform: scaleY(0.5);
			transform: scaleY(0.5)
		}
	}
}

.border-bottom-1px-dotted(@color){
	position: relative;
	&:after{
		content: '';
		display: block;
		position: absolute;
		width: 100%;
		left: 0;
		bottom: 0;
		border-bottom: 1px dotted @color; 
		@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5){
			-webkit-transform: scaleY(0.7);
			transform: scaleY(0.7);
		}

		@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2){
			-webkit-transform: scaleY(0.5);
			transform: scaleY(0.5)
		}
	}
}

.border-none{
	&:before{
		display: none;
	}
	&:after{
		display: none;
	}
}




