.dest .nav .mint-navbar {
  height: 70px;
}
.dest .nav .mint-navbar .mint-tab-item {
  margin: 0;
  padding: 0;
}
.dest .nav .mint-navbar .mint-tab-item.is-selected {
  border-bottom: 2px solid #fe6f69;
}
.dest .nav .mint-navbar .mint-tab-item.is-selected .mint-tab-item-label {
  color: #fe6f69;
}
.dest .nav .mint-navbar .mint-tab-item .mint-tab-item-icon {
  display: block;
  margin: 0 auto 10px;
  padding: 0;
  background-repeat: no-repeat;
}
.dest .nav .mint-navbar .mint-tab-item .mint-tab-item-label {
  color: #666;
}
.dest .content h3.title {
  line-height: 15px;
  font-size: 15px;
  color: #363636;
  margin: 0 0 13px 0;
  text-indent: 12px;
  font-weight: normal;
}
.dest .content .hot-dest .container {
  height: 85px;
  overflow: hidden;
  margin-bottom: 30px;
  padding: 0 0 0 10px;
}
.dest .content .hot-dest .container .swiper-slide {
  width: 125px;
  height: 85px;
  margin-right: 10px;
}
.dest .content .hot-dest .container .swiper-slide img {
  display: block;
  width: 100%;
  height: 63px;
}
.dest .content .hot-dest .container .swiper-slide span {
  display: inline-block;
  height: 13px;
  line-height: 13px;
  font-size: 13px;
  color: #666666;
  margin-top: 9px;
}
.dest .content .all-dest h3.title {
  margin-bottom: 20px;
}
.dest .content .all-dest .container {
  width: 98%;
  margin: 0 auto;
}
.dest .content .all-dest .container h4.continent {
  height: 30px;
  line-height: 30px;
  font-size: 12px;
  color: #666;
  background-color: #f0f0f0;
  margin: 0;
  text-indent: 10px;
  font-weight: normal;
}
.dest .content .all-dest .container .list dl {
  width: 96%;
  margin: 0 auto;
  border-bottom: 1px solid #dfdfdf;
}
.dest .content .all-dest .container .list dl:last-child {
  border: none;
}
.dest .content .all-dest .container .list dl dt,
.dest .content .all-dest .container .list dl dd {
  height: 44px;
  line-height: 44px;
  font-size: 14px;
  width: 94%;
  margin: 0 auto;
}
.dest .content .all-dest .container .list dl dt {
  color: #363636;
}
.dest .content .all-dest .container .list dl dt i.arrow {
  float: right;
  width: 9px;
  height: 5px;
  background-repeat: no-repeat;
  background-size: 9px 5px;
  background-image: url('down.png');
  margin: 19px 10px 0 0;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2), only screen and (min-device-pixel-ratio: 2) {
.dest .content .all-dest .container .list dl dt i.arrow {
    background-image: url('down@2x.png');
}
}
@media only screen and (-webkit-min-device-pixel-ratio: 3), only screen and (min-device-pixel-ratio: 3) {
.dest .content .all-dest .container .list dl dt i.arrow {
    background-image: url('down@3x.png');
}
}
.dest .content .all-dest .container .list dl dt i.arrow.up {
  background-image: url('up.png');
}
@media only screen and (-webkit-min-device-pixel-ratio: 2), only screen and (min-device-pixel-ratio: 2) {
.dest .content .all-dest .container .list dl dt i.arrow.up {
    background-image: url('up@2x.png');
}
}
@media only screen and (-webkit-min-device-pixel-ratio: 3), only screen and (min-device-pixel-ratio: 3) {
.dest .content .all-dest .container .list dl dt i.arrow.up {
    background-image: url('up@3x.png');
}
}
.dest .content .all-dest .container .list dl dd {
  text-indent: 12px;
  color: #666;
  position: relative;
}
.dest .content .all-dest .container .list dl dd:before {
  content: '';
  display: block;
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  border-top: 1px dotted #dfdfdf;
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.dest .content .all-dest .container .list dl dd:before {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.dest .content .all-dest .container .list dl dd:before {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
.dest .content .all-dest .container .list dl .isShow-enter-active,
.dest .content .all-dest .container .list dl .isShow-leave-active {
  -webkit-transition: all .5s;
  transition: all .5s;
}
.dest .content .all-dest .container .list dl .isShow-enter,
.dest .content .all-dest .container .list dl .isShow-leave-to {
  height: 0;
  opacity: 0;
}



